
import axios from 'axios'
import * as Util from '@/util/util'
import * as Log from '@/util/log'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'


export default {
components: {
Swiper,
SwiperItem,
XImg
},
data() {
return {
loading: true,
loadSuccess: false,
currentIndex: 0,
imageList: []
}
},
activated() {
Log.log('activated')
axios.get(this.getAlbumPicsUrl(this.$route.params.feedId))
.then(response => {
Log.log(response)
this.loading = false
this.loadSuccess = true
this.imageList.push(...response.data)
}).catch(err => {
Log.log(err)
this.loading = false
this.loadSuccess = false
})
},
deactivated() {
Log.log('deactivated')
this.loading = true
this.loadSuccess = false
this.currentIndex = 0
this.imageList = []
},
methods: {
getAlbumPicsUrl(feedId) {
return Util.getAlbumPicsUrl(feedId)
},
getProxyUrl(rawUrl) {
return Util.getProxyUrl(rawUrl)
},
onIndexChanged(index) {
Log.log(`onIndexChanged:${index}`)



this.$refs.swiperItems[index].$children[0].blazy.load(this.$refs.swiperItems[index].$children[0].$el, true)
}
}
}



